a {
    text-decoration: none;
  }
  
  .sidenav {
      height: 90px; /* Full-height: remove this if you want "auto" height */
      width: 100%; /* Set the width of the sidebar */
      position: fixed; /* Fixed Sidebar (stay in place on scroll) */
      z-index: 2; /* Stay on top */
      top: 0; /* Stay at the top */
      left: 0;
      background-color: #3d3c3c; /* Black */
      margin: 0;
      overflow-y: hidden; /* Disable horizontal scroll */
      
    }
    
    /* The navigation menu links */
    .sidenavElements {
      margin: 0;
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-inline-start: 0px;
      margin-inline-end: 0px;
      
  
    }
    .sidenavElements li {
      list-style-type: none;
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center;
      width: 100px;
      height: 90px;
      font-size: 0.85em;
    }
    .sidenavElements li > a{
      display: block;
      width: 100%;
      height: 100%;
      
    }
    .sidenavElements li > a > span{
      height:100%;
      display: flex;
      align-items: center;
      justify-content: center;
      
    }
    /* When you mouse over the navigation links, change their color */
    .sidenavElements span {
      width: 100%;
      color: rgb(222, 222, 222);
      display: block;
    }

  
    .sidenavElements>li:hover{
      cursor: pointer;
      background-color: #484848;
    }
  
  
  
  
  
  
    .nav {
      height: 100px;/* Full-height: remove this if you want "auto" height */
      width: 100%; /* Set the width of the sidebar */
      position: fixed; /* Fixed Sidebar (stay in place on scroll) */
      z-index: 2; /* Stay on top */
      top: 0; /* Stay at the top */
      left: 0;
      background-color: #111; /* Black */
      overflow-x: hidden; /* Disable horizontal scroll */
      padding-left: 20px;
      display: flex;
    }
    

  
    
    /* On smaller screens, where height is less than 450px, change the style of the sidebar (less padding and a smaller font size) */
    @media screen and (max-height: 450px) {
      .sidenav {padding-top: 15px;}
      .sidenav a {font-size: 18px;}
    }