.centerPages {
    position: absolute;
    top:  50%;
    left: calc(50% + 100px);
    margin-right: -50%;
    transform: translate(-50%, -50%);
    text-align: center;
    
}

.period {
    position: relative;
    top: 120px;
    left: 50px;
    
}

.forma{
    display: flex;
    flex-direction: column;
    width: 40%;
    font-size: 20px;
}

.button1{
    border: solid 1px black;
    padding: 5px;
    
    width: fit-content;
    height: fit-content;
    color: white;
    background-color: #3d3c3c;
    font-size: 20px;
    cursor: pointer;
}

.result {
    padding: 2px;
    width: 40%;
    border: solid 1px black;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    
}
.result p{
    margin: 2px;
    border-bottom: 1px solid black;
}


.dopModule{
    display: flex;
    flex-direction: column;
    width: 40%;
    font-size: 20px;
    position: absolute;
    top: 120px;
    left: 50%;
}

.dopModule button{
    margin: 20px 0;
}

.resultMod{
    padding: 2px;
    border: solid 1px black;
    display: flex;
    flex-direction: column;
}

.buttons{
    display: flex;
    justify-content: space-between;
}